import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import one from '@/components/one'
import three from '@/components/three'
import six from '@/components/six'
import seven from '@/components/seven'
import eight from '@/components/eight'
import resume from '@/components/resume'
import music from '@/components/music'
import design from '@/components/design'
import japanese from '@/components/japanese'
import note from '@/components/note'

Vue.use(Router)

export default new Router({
  //vue-router的几种模式
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },{
      path: '/one',
      name: 'one',
      component: one
    },{
      path:'/three',
      name:'three',
      component:three
    },{
      path:'/six',
      name:"six",
      component:six
    },{
      path:"/seven",
      name:"seven",
      component:seven
    },{
      path:"/eight",
      name:"eight",
      component:eight
    },{
      path:"/resume",
      name:"resume",
      component:resume
    },{
      path:"/music",
      name:"music",
      component:music
    },{
      path:"/design",
      name:"design",
      component:design
    },{
      path:"/ya.html",
      name:"music",
      component:music
    },{
      path:"/japanese",
      name:"japanese",
      component:japanese
    },{
      path:"/note",
      name:"note",
      component:note
    }
  ]
})



