<template>
    
        <div class="box" id="box"> 
            <div class="d_imgBox" id="item" v-for="item in items">
                <img v-bind:src="item.src">  
                <div class="cover">
                    <div class="time">
                    {{item.time}}
                    </div>
                </div>                  
            </div>
 
        </div>
    

</template>


<script>

    export default {
        name:'three',
        data () {
            return {
                items:[
                    {"src": "../../static/y1.png","time" : "2017-4-4" },

                    {"src": "../../static/design/ji.png","time" : "2019-1-31" },
                    {"src": "../../static/design/d1.gif","time" : "2018-4-4" },
                    {"src": "../../static/design/d3.png","time" : "2018-4-4" },
                    {"src": "../../static/design/san.png","time" : "2019-4-10" },



                    {"src": "../../static/design/baby2.png","time" : "2019-1-31" },

                    {"src": "../../static/design/zz.png","time" : "2019-1-31" },
                    {"src": "../../static/design/d2.gif","time" : "2018-4-4" },

                    {"src": "../../static/design/三只2.png","time" : "2019-1-31" },
                     ]
            };
        },
        methods:{
            
        },
        mounted() {
            var box = document.getElementById('box');
            var items = box.children;
            // 定义每一列之间的间隙 为10像素
            var gap = 20;

        
        // 一进来就调用一次
        
            // this.$nextTick(() => {
        waterFall();    
        
    //   })


        // 封装成一个函数
        function waterFall() {
            // 1- 确定列数  = 页面的宽度 / 图片的宽度
            var pageWidth = (getClient().width)*0.75;
            // var itemWidth = items[0].offsetWidth;
            var itemWidth = pageWidth * 0.3;
            var columns = parseInt(pageWidth / (itemWidth + gap));
            var arr = [];
            for (var i = 0; i < items.length; i++) {
                if (i < columns) {
                    // 2- 确定第一行
                    items[i].style.top = 0;
                    items[i].style.left = (itemWidth + gap) * i + 'px';
                    arr.push(items[i].offsetHeight);

                } else {
                    // 其他行
                    // 3- 找到数组中最小高度  和 它的索引
                    var minHeight = arr[0];
                    var index = 0;
                    for (var j = 0; j < arr.length; j++) {
                        if (minHeight > arr[j]) {
                            minHeight = arr[j];
                            index = j;
                        }
                    }
                    // 4- 设置下一行的第一个盒子位置
                    // top值就是最小列的高度 + gap
                    items[i].style.top = arr[index] + gap + 'px';
                    // left值就是最小列距离左边的距离
                    items[i].style.left = items[index].offsetLeft + 'px';

                    // 5- 修改最小列的高度 
                    // 最小列的高度 = 当前自己的高度 + 拼接过来的高度 + 间隙的高度
                    arr[index] = arr[index] + items[i].offsetHeight + gap;
                }
            }
        }
        // 页面尺寸改变时实时触发
        window.onresize = function() {
            waterFall();
        };
        // 当加载到第30张的时候

    

    // clientWidth 处理兼容性
    function getClient() {
        return {
            width: window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth,
            height: window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
        }
    }
    // scrollTop兼容性处理
    function getScrollTop() {
        return window.pageYOffset || document.documentElement.scrollTop;
    }

        }
    };
</script>  


<style scoped>

.box{
    width:1160px;
    height:100%;
}
.d_imgBox{
    /*
    width:22%;
    margin-right:2.5%;
    overflow:hidden;
    float:left;
    margin-bottom:20px;*/

    /*box-shadow: 1px 1px 1px #459ad6;*/
    position: absolute;
}
.d_imgBox img{

    /*width:100%;
    min-width:256px;*/


    width: 290px;
    display: block;
    animation-play-state: paused;
}

.cover{
    width:100%;
    height:100%;
    position:absolute;
    left:0px;
    bottom:0px;
    z-index:2;
    background: linear-gradient(to bottom, rgba(0,0,0,0) ,#f8d4cb );
    opacity:0.6;
    transition: all 0.4s;
    pointer-events:none;
}
#item:hover .cover{
    opacity:0;
}
.cover .time{
    color:#fff;
    position:absolute;
    right:40%;
    bottom:15px;
    font-size:14px;
    z-index:3;
}
</style>


