<template>
    
        <div class="m_box">
            <ul v-on:click.prevent="showBigImg">
                <li v-for="item in items">
                    <div class="pu">
                        <img v-bind:src=" item.src ">
                    </div>
                    <div class="cover">
                        <p>点击查看</p>
                    </div> 
                </li>        
            </ul>
            <div class="under" v-show="show">
                <div class="shut el-icon-circle-close" v-on:click = "shutBigImg"></div>
                <img class="big_img" v-bind:src="bigImg">
            </div>
        </div>

    

</template>


<script>

    export default {
        name:'three',
        data () {
            return {
                show:false,
                bigImg:"",
                items:[{"src":"../../static/songbie.png"} , {"src":"../../static/aierlan.png"} , {"src":"../../static/bdbyb.png"},{"src":"../../static/tkzc.png"}]
            };
        },
        methods:{
            showBigImg:function(event){
                //console.log(this.show);
                //console.log(event.target.src);
                this.show = true;
                this.bigImg = event.target.src;
                document.querySelector('body').style.overflow = 'hidden';
            },
            shutBigImg:function(){
                this.show = false;
                document.querySelector('body').style.overflow = 'auto';

            }
        },
        mounted() {


        }
    };
</script>  


<style scoped>
.m_box{
    /*background:linear-gradient(to bottom, rgba(0,0,0,0) ,rgba(0,0,0,.6));*/
}
ul{
    
}
.under{
    width:100%;
    height:100%;
    border:1px solid #aaa;
    position:fixed;
    text-align:center;
    top:0px;
    left:0px;
    background-color: rgba(12,15,19,.8);
    z-index:5;
    overflow-y: auto;
}
.under .big_img{
    margin-top:80px;
    margin-bottom:80px;
    padding:30px 30px ;
    background-color:#d3d3d3;
}
.under .shut{
    width:20px;
    height:20px;
    position:fixed;
    right:200px;
    top:60px;
    color:#fff;
}
li{
    width:48%;
    height:300px;
    overflow:hidden;
    float:left;
    list-style:none;
    margin-right:2%;
    overflow:hidden;
    position:relative;
    margin-bottom:40px;
    box-shadow:0px 0px 4px #eee;
}
li img{
    width:100%;
    /*transform:rotate(7deg);*/
}
.pu{
    z-index:1;
}
.cover{
    width:100%;
    height:50%;
    position:absolute;
    left:0px;
    bottom:0px;
    z-index:2;
    /*background: linear-gradient(to bottom, rgba(0,0,0,0) ,rgba(0,0,0,0.6) );*/
    background: linear-gradient(to bottom, rgba(0,0,0,0) ,#e8785e );
    opacity:0.6;
    transition: all 0.4s;
    pointer-events:none;
}
.cover p{
    width:100%;
    margin-top:150px;
    text-align:center;
    color:#fff;
    display:none;
    z-index:9;
}
li:hover .cover{
    height:100%;
    /*background: linear-gradient(to bottom, rgba(0,0,0,0.2)  , #232A34);*/
    background: linear-gradient(to bottom, rgba(0,0,0,0)  , #37948a);

}
li:hover p{
    display:block;
}
</style>


