<template>
    
        <div class="r3box">
            <h3 class="r3_title">
                娅学习的日语日常用语，仅仅满足自我兴趣   
            </h3>
            <div class="r3_introduction">
                记录日语常用短语，带读音和中文翻译
            </div>  
            <div class="r3_code_box" v-for="item in items">
                <div class="j_box">
                    <div class="num">
                        {{item.n}}
                    </div>
                    <div class="p">
                        {{item.p}}
                    </div>
                    <div class="c">
                        {{item.c}}
                        <div class="p read">
                            {{item.o}}
                        </div>
                    </div>

                </div>
                
            </div>
        </div>
    

</template>


<script>

    export default {
        name:'japanese',
        data () {
            return {
                items:[],
            };
        },
        mounted() {
            axios("static/japanese.json").then(res => {
                var a =  res.data.data;
                //console.log(a)
                for(var i = 0;i< a.length; i++){
                    a[i].n = a[i].n;
                    a[i].c= a[i].c;
                    a[i].p = a[i].p;
                    a[i].o = a[i].o;

                }
                this.items = a;
                //console.log(JSON.stringify(this.items));

            })
        }
    };
</script>  


<style scoped>
    .j_box{
        border-bottom:1px solid #E2E3E5;
        margin-bottom:40px;
        /*background-image:linear-gradient(#fff,#a6acac);*/
        box-shadow:0px 0px 5px #a6acac;
        width:48%;
        position:relative;
    }
    .j_box:hover{
        width:50%; 
        margin-left:-10px;       
    }
    .num{
        position:absolute;
        right:5px;
        top:5px;
        color:#444;
        font-size:8px;
    }
    .c{
        padding:0px;
        height:40px;
        line-height:40px;
        color:#e16c49;
        background-color:#eee;
    }
    .read{
        display:none;

    }
    .j_box:hover .read{
        display:block;
    }
    .p{
        height:30px;
        line-height:30px;  
        margin-bottom:0px;
    }

</style>


