<template>
  <div id="app">
    <div id="logo">
      <h1 class="motto">不积跬步无以至千里</h1>
      <div class="imgBox">
        <router-link to="/"><div class="front">
        
        </div></router-link>
          
        <router-link to="/"><div class="back">

        </div></router-link>
      </div>
      <div class="contact_box">
        <div class="qq">
          <div class="qr_code"></div>  
        </div>
        <div class="weixin">
          <div class="qr_code"></div>  
        </div>
        <router-link to="/resume"><div class="resume">关于</div></router-link>
      </div> 
      <div class="classify">
        <p>
          娅的笔记        
        </p>
      </div>
      <ul>
        <router-link to="/one"><li :class="{act:num === 1}" @click="active1()">&nbsp;小游戏</li></router-link>
        <li :class="{act:num === 2}" @click="active2()">&nbsp;css</li>
        <router-link to="/three"><li :class="{act:num === 3}" @click="active3()">&nbsp;js函数</li></router-link>
        <router-link to="/japanese"><li :class="{act:num === 4}" @click="active4()">&nbsp;日本語</li></router-link>
        <li :class="{act:num === 5}" @click="active5()">&nbsp;English</li>
        <router-link to="/six"><li :class="{act:num === 6}" @click="active6()">&nbsp;vue踩坑记录</li></router-link>
        <router-link to="/seven"><li :class="{act:num === 7}" @click="active7()">&nbsp;iframe那些事儿</li></router-link>
        <router-link to="/eight"><li :class="{act:num === 8}" @click="active8()">&nbsp;重学js</li></router-link>
      </ul>
    </div>

    <div class="r3">
    
      <router-view/>
    </div>


  </div>
</template>



<script>
export default {
  name: 'App',
  data(){
    return {
      num :0,
    }
  },
  methods:{
    active1:function(){
      this.num = 1;
    },
        active2:function(){
      this.num = 2;
    },
      active3:function(){
      this.num = 3;
    },
        active4:function(){
      this.num = 4;
    },
        active5:function(){
      this.num = 5;
    },
        active6:function(){
      this.num = 6;
    },
        active7:function(){
      this.num = 7;
    },
        active8:function(){
      this.num = 8;
    },
        active9:function(){
      this.num = 9;
    }

  },
  created(){
        //loading
    document.onreadystatechange = function () {
      //interactive
      if (this.readyState === 'complete') {
        document.querySelector('.loading').style.display = 'none';
      }
    }
  }
}


</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /*margin-top: 60px;*/
  position:absolute;
  z-index:1;
}

#app #logo ul .act{
    background-color:#444444 ; 
    color : #fff ;
}
</style>



