<template>
    <div id="note">
        <div class="imgB"></div>
        <div class="noteBox">
            <div class="title">第一篇日记</div>
            <div class="time">2019/4/10</div>
            <div class="brief">这是随笔开通的第一天，在这里记录一下，好开心啊，希望自己变得越来越好,记录人生中美好的瞬间也是一件快乐的事情</div>
        </div>
    </div>
</template>

<script>
export default{
    name:'note',
}

</script>


<style scoped>

#note{
    width:143.4%;
    height:100%;
    margin-top:-100px;
    margin-left:-22%;
}
.imgB{
    width:100%;
    height:100%;
    background-image:url("../assets/hh3.png");
    overflow-x:hidden;
    background-position:center;
}
.noteBox{
    width:75%;
    max-width:1220px;
    border:1px solid #d3d3d3;
    box-shadow:1px 1px 10px #eee;
    position:absolute;
    left:14.5%;
    padding:0 20px;
    margin-top:40px;
    margin-bottom:40px;

}
.noteBox:hover{
    box-shadow:1px 1px 10px #d3d3d3;
}
.title{
    color:#37948a;
    text-align:left;
    fon-size:24px;
    margin-top:20px;
    margin-bottom:10px;
}
.time{
    color:#a6acac;

}
.brief{
    color:#343434;
    margin-top:10px;
    margin-bottom:20px;
}
</style>


