<template>
    
        <div class="r3box">
            <h3 class="r3_title">
                娅使用iframe遇到的问题  
            </h3>
            <div class="r3_introduction">
                记录娅使用iframe遇到的问题以及解决方案和拓展
            </div>  
            <div class="r3_code_box" v-for="item in items">
                <h4 class="p">
                    {{item.p}}
                </h4>
                <div class="p">
                    {{item.t}}
                </div>
                <div class="c">
                    <pre>
                        {{item.c}}
                    </pre>
                </div>
                
            </div>
            <img src="../assets/goTop1.png" class="i1">
            <img src="../assets/goTop2.png" class="i2">
        </div>
    

</template>


<script>

    export default {
        name:'seven',
        data () {
            return {
                items:[]
            };
        },
        mounted() {
            axios("static/iframe.json").then(res => {
                var a =  res.data.data;
                //console.log(a)
                for(var i = 0;i< a.length; i++){
                    a[i].c= unescape(a[i].c);
                    a[i].p = a[i].p;
                    a[i].t = a[i].t;
                }
                this.items = a;
                //console.log(JSON.stringify(this.items));

            })
        }
    };
</script>  


<style scoped>

</style>


