<template>
  <div class="hello r3box">
    <ul class="carousel" id="imgContainer">
      <a href="./ya.html"><li class="design"><img src="../assets/family4.png"></li></a>
      <a href="./ya.html"><li class="music"><img src="../assets/music3.png"></li></a>

      
    </ul>
    <ul class="circle" id="circle">
      <li id="cc"></li>
      <li id="cc"></li>

    </ul>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to ya`s world !'
    }
  },
  mounted() {
    var carousel = document.querySelector('.carousel');
        var imgContainer = document.getElementById("imgContainer");
        var circle = document.getElementById("circle");

        var imgList = imgContainer.getElementsByTagName("img");
        var ccList = circle.getElementsByTagName("li");

        var active_img = imgList[0];
        active_img.className = 'active';

        var active_cc = ccList[0];
        active_cc.className = 'active2';

        var i = 0 ;
        var len = imgList.length - 1;

        var autoPlay = function(i){
            if(i >= len){
                active_img = imgList[0];
                imgList[len].className = '';
                imgList[0].className = 'active';

                active_cc = ccList[0];
                ccList[len].className = '';
                ccList[0].className = 'active2';
            }else{
                active_img = imgList[i+1];
                imgList[i].className = '';
                imgList[i+1].className = 'active';

                active_cc = ccList[i+1];
                ccList[i].className = '';
                ccList[i+1].className = 'active2';
            }
        }
        setInterval(function(){
            autoPlay(i);
            if(i < len){
                i++;
            }else{
                i = 0;
            }
        },4000)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.hello{
    /*background-image:url("../assets/ql.png");*/
    background-position:240px -120px;
    background-repeat: no-repeat;
    height:600px;
}
h1, h2 {
  font-weight: normal;
}
.r3box{
  border:0;
}
ul {
  list-style-type: none;
  padding: 0;
  width:100%;
  height:100%;
  overflow:hidden;
}
ul img {
  width:800px;
  height:600px;
  display: inline-block;
  margin: 0 10px;
  float:left;
  position:absolute;
  opacity:1;
  overflow:hidden;

}
.active{
  opacity:0;
}


.circle{
  width:100px;
  height:20px;
  list-style:none;
  position:relative;
  left:400px;
  bottom:40px;
}

.circle li{
  width:10px;
  height:10px;
  border-radius:10px;
  background-color:#aaa;
  float:left;
  margin-left:10px;
}

.circle .active2{
  background-color:#fff;
}


</style>



