<template>
    <div class="r3box">
            <h3 class="r2_title">
                {{message}}
            </h3>
            <div>
                <button class="start">start</button>
                <div class="time">
                    00 分
                </div>
                <button class="end">end</button>
            </div>  
            <div class="r2_container">
                <ul class="r1" id = "ul">
                    <li class="r1_li"><img src="../assets/cc2.png" class="i img1"></li>
                    <li class="r1_li"><img src="../assets/cc2.png" class="i img2"></li>
                    <li class="r1_li"><img src="../assets/cc2.png" class="i i3"></li>
                    <li class="r1_li"><img src="../assets/cc2.png" class="i i4"></li>
                </ul>
            </div> 
    </div>
</template>

<script>
    export default {
        name:'one',
        data () {
            return {
                message: '找出头上有犄角的猫猫'
            };
        },
        mounted () {

      var ti = document.querySelector(".r1");
      var a = 0;
      ti.addEventListener("click", function (event) {
        if (event.target.src.indexOf("cc1") !== -1) {
          event.target.src = s0;
          document.querySelector(".time").innerHTML = a + 1 + " 分";
          a++;
        }
      })


      var s0 = "../static/rt.png";
      var s1 = "../static/cc1.png";
      var s2 = "../static/cc2.png";


      document.querySelector(".start").addEventListener('click' , function () {




        var r1ul = document.querySelectorAll(".r2_container")[0];
        r1ul.onmousemove = function () {
          this.style.cursor = "url('../static/cursor.ico'),auto";
        }



        var t = setInterval(function () {
          var iL = document.querySelectorAll(".i");
          var li = document.querySelectorAll(".r1_li");
          for (var i = 0; i < li.length; i++) {
            // console.log(iL[i].getBoundingClientRect().top -li[i].getBoundingClientRect().top)

            if (iL[i].getBoundingClientRect().top - li[i].getBoundingClientRect().top > 146) {
              // console.log(">" + i)
              iL[i].src = s2;
            }
          }
        }, 100)


        var iL = document.querySelectorAll(".i");
        for (var i = 0; i < iL.length; i++) {
          iL[i].className = "i ani"
        }

            //cc1随机位置出现
        var it = setInterval(function cc1() {
          var z = parseInt(Math.random() * 5);
          if (z > 3) {
            z = 0;
          }
          var iL = document.querySelectorAll(".i");
          iL[z].src = s1;
        }, 4000);

        document.querySelector(".end").addEventListener('click',function () {
          window.clearInterval(it);
          window.clearInterval(t);
          var iL = document.querySelectorAll(".i");
          for (var i = 0; i < iL.length; i++) {
            iL[i].className = "i"
          }
        })
      })
        },
        
    };

</script>  


<style scoped>
.r2_container{
  width:400px;
  height:440px;
}
</style>


