<template>
    
        <div class="r3box">
            <h3 class="r3_title">
                娅碰到的有代表性的js面试题    
            </h3>
            <div class="r3_introduction">
                记录娅遇到的有相关js基础知识点的面试题及分析过程，目的在于夯实基础
            </div>  
            <div class="r3_code_box" v-for="item in items">
                <h4 class="p">
                    {{item.p}}
                </h4>
                <div class="c">
                    <pre>
                        {{item.c}}
                    </pre>
                </div>
                
            </div>
            <img src="../assets/cc1.png" class="i1">
            <img src="../assets/cc1.png" class="i2">
        </div>
    

</template>


<script>

    export default {
        name:'three',
        data () {
            return {
                items:[]
            };
        },
        mounted() {
            axios("static/js-qa.json").then(res => {
                var a =  res.data.data;
                //console.log(a)
                for(var i = 0;i< a.length; i++){
                    a[i].c= unescape(a[i].c);
                    a[i].p = a[i].p;
                }
                this.items = a;
                //console.log(JSON.stringify(this.items));

            })
        }
    };
</script>  


<style scoped>


</style>


